<template>
  <div class="hello">
    <el-dialog
  title="Groups"
  :visible.sync="dialogVisible"
  fullscreen
 center>
  <el-card   class="card2" v-for="(group,index) in groups" :key="group.map((player,i)=>`group_card${player.id}${i}`).join('')">
      <div slot="header" class="clearfix">
    <span>Group {{parseInt(index)+1}}</span>
  </div>
  <div  v-for="(player,playerIndex) in group" :key="`$player_row_in_card{player.id}${playerIndex}`" class="text item">
    {{player.id}}
  </div>
    </el-card>
</el-dialog>
  <h1 class="title">
    <img class="vue-logo" src="https://vuejs.org/images/logo.png" />
    Card Shuffling
  </h1>
  <div class="speed-buttons">
    <el-button v-for="type in shuffleTypes"
            class="button is-small"
            :key="type"
            :class="{ 'is-light': shuffleSpeed != `shuffle${type}` }"
            @click="shuffleSpeed = `shuffle${type}`">
      {{ type }}
    </el-button>
      <!-- <span >Minimum Number Of Players Per Group </span>
      <el-input-number size="mini" controls-position="right"  placeholder="New Player" v-model="minimum" ></el-input-number> -->
      <span >Maximum Number Of Players Per Group </span>
      <el-input-number size="mini" controls-position="right" placeholder="New Player" v-model="maximum" ></el-input-number>

  </div>
  <div class="main-buttons">
    <!-- <el-button v-if="isDeckShuffled" @click="displayInitialDeck" class="button is-primary is-outlined">
      Reset <i class="fas fa-undo"></i>
    </el-button> -->
    <el-button @click="shuffleDeck" class="button is-primary">
      Shuffle <i class="fas fa-random"></i>
    </el-button>
  </div>
  <transition-group :name="shuffleSpeed" tag="div" class="deck">
    <el-card class="card" v-for="card in cards" :key="card.id">
      <span >{{ card.id }} </span>
       <el-button  @click="removePlayer(card.id)" type="danger" icon="el-icon-delete" circle></el-button>
    </el-card>
    <el-card class="card" :key="'input_card'">
      <el-input @keyup.enter.native="addPlayer" placeholder="New Player" v-model="newPlayerInput" ></el-input>
    </el-card>
  </transition-group>
  </div>
</template>

<script>
import * as _ from 'lodash';
import  * as faker from 'faker';
export default {
  data:function() {return {
    dialogVisible:false,
    groups:[],
    newPlayerInput:"",
    defaultPlayers: ['whrrmydragons', 'InDoNax', 'ProductiveBear', 'ElectricKnob','xlad'],
    cards: [],
    maximum:3,
    shuffleSpeed: 'shuffleMedium',
    shuffleTypes: ['Slow', 'Medium', 'Fast'],
    isDeckShuffled: false,
    shuffleCount: 0,
  }},
  created() {
    this.displayInitialDeck();
    this.createGroups();
  },  
  methods: {
    displayInitialDeck() {
      this.cards = [];

        for( let r = 0; r < this.defaultPlayers.length; r++ ) {
          let card = {
            id:  this.defaultPlayers[r]
          }
          this.cards.push(card);
        }
      

      this.isDeckShuffled = false;
      this.shuffleCount = 0;
    },
    createGroups(){
      let shuffledPlayers = _.shuffle(this.cards);
      const numOfGroups = _.ceil(shuffledPlayers.length/this.maximum);
      const lastTeamIsSingle = shuffledPlayers.length%this.maximum ===1;
      let groupsArray = _.map(shuffledPlayers,(player,i)=>{return {...player,group:_.floor(i/this.maximum)}});
      if(lastTeamIsSingle)groupsArray[groupsArray.length-2].group++; // if there is a single person in the last group add a person from the previous group
      const groupedGroupsArray = _.groupBy(groupsArray,player=>player.group);
      console.log(groupedGroupsArray);
      this.$set(this,"groups",groupedGroupsArray);
      console.log(`last team is single:${lastTeamIsSingle}`)
    },
    removePlayer(playerID){

this.$set(this,"cards",_.filter(this.cards,card=>card.id!==playerID));
    },
    addPlayer(){
// console.log(this.newPlayerInput);
this.cards.push({id:this.newPlayerInput})
this.$set(this,"cards",this.cards);
this.$set(this,"newPlayerInput","");
    },
    shuffleDeck() {  
      this.createGroups();
      this.dialogVisible=true;      
      // for(let i = this.cards.length - 1; i > 0; i--) {
      //   let randomIndex = Math.floor(Math.random() * i);
        
      //   let temp = this.cards[i];
      //   this.$set(this.cards, i, this.cards[randomIndex]);
      //   this.$set(this.cards, randomIndex, temp);
      // }

      // this.isDeckShuffled = true;
      // this.shuffleCount = this.shuffleCount + 1;
    }
  }
};
</script>

